<template>
  <div class="home">
    <div class="parent__form">
      <h2 class="title parent__title">Персональные данные</h2>
      <label class="home__label home__label--margin">
        <span class="home__descr">Имя</span>
        <input class="parent__input home__input" type="text" name="Имя" v-model="userData.name">
      </label>
      <label class="home__label">
        <span class="home__descr">Возраст</span>
        <input class="parent__input home__input" type="number" name="Возраст" min="18" v-model="userData.age">
      </label>
    </div>
    <div class="child__form">
      <div class="child__wrapper">
        <h2 class="title child__title">Дети (макс. 5)</h2>
        <button class="btn add-button" v-show="childrenAmountMax > 0"
          @click="addChild(), childrenAmountMax--">
          <span>
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path fill-rule="evenodd" clip-rule="evenodd"
                d="M5.13332 10.8556C4.50125 10.8555 3.98887 11.3679 3.98887 12C3.98887 12.6321 4.50126 13.1445 5.13332 13.1445L10.8554 13.1445L10.8554 18.8668C10.8554 19.4989 11.3678 20.0113 11.9999 20.0113C12.632 20.0113 13.1444 19.4989 13.1444 18.8668L13.1443 13.1445L18.8667 13.1445C19.4988 13.1445 20.0112 12.6321 20.0112 12C20.0112 11.3679 19.4988 10.8556 18.8667 10.8556L13.1443 10.8556L13.1443 5.13338C13.1443 4.50132 12.632 3.98893 11.9999 3.98893C11.3678 3.98893 10.8554 4.50131 10.8554 5.13338L10.8554 10.8556L5.13332 10.8556Z"
                fill="#01A7FD" />
            </svg>
          </span>
          <span>
            Добавить ребенка
          </span>
        </button>
      </div>
      <div class="child__card" v-for="child in userData.children" :key="child.id">
        <label class="home__label">
          <span class="home__descr">Имя</span>
          <input class="child__input home__input" type="text" name="Имя" v-model="child.childName" required="true">
        </label>
        <label class="home__label">
          <span class="home__descr">Возраст</span>
          <input class="child__input home__input" type="number" name="Возраст" v-model="child.childAge" required="true">
        </label>
        <button class="btn delete-button" @click="deleteChild(child.id), childrenAmountMax++">Удалить</button>
      </div>
    </div>
    <button class="btn save-button" @click="saveData(userData)">Сохранить</button>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';


export default {
  data() {
    return {
      childrenAmountMax: null,
    };
  },
  methods: {
    addChild() {
      return this.userData.children.push({ id: this.userData.children.reduce((acc, curr) => acc.id > curr.id ? acc.id : curr.id + 1, 1) });
    },
    saveData(data) {
      this.$store.commit('saveData', data);
    },
    deleteChild(childId) {
      this.userData.children = this.userData.children.filter(child => child.id !== childId);
    },
  },
  computed: {
    ...mapGetters({ userData: 'getUserData' }),
  },
  created() {
    this.childrenAmountMax = 5 - this.userData.children.length;
  }
}
</script>
