import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    userData: {
      name: 'Петр',
      age: 99,
      children: [
        {
            id: 1,
            childName: 'Коля',
            childAge: 10,
        },
        {
            id: 2,
            childName: 'Вася',
            childAge: 15,
        },
        {
            id: 3,
            childName: 'Саша',
            childAge: 4,
        },
      ],
    },
  },
  getters: {
    getUserData(state) {
      return JSON.parse(JSON.stringify(state.userData));
    },
  },
  mutations: {
    saveData(state, userData) {
      state.userData = JSON.parse(JSON.stringify(userData));
    },
  },
  actions: {
  },
  modules: {
  }
})
